import { useState } from "react";
import { Link } from "react-router-dom";
import { Row, Col, Drawer, Image } from "antd";
import { withTranslation } from "react-i18next";
import Container from "../../common/Container";
import { Button } from "../../common/Button";
import { SvgIcon } from "../../common/SvgIcon";
import {
  HeaderSection,
  LogoContainer,
  Burger,
  NotHidden,
  Menu,
  CustomNavLinkSmall,
  Label,
  Outline,
  Span,
} from "./styles";
import "./style.css";

const Header = ({ t }: any) => {
  const [visible, setVisibility] = useState(false);

  const showDrawer = () => {
    setVisibility(!visible);
  };

  const onClose = () => {
    setVisibility(false);
  };

  const MenuItem = () => {
    const scrollTo = (id: string) => {
      const element = document.getElementById(id) as HTMLDivElement;
      element.scrollIntoView({
        behavior: "smooth",
      });
      setVisibility(false);
    };
    return (
      <>
        <Link to="/about" onClick={onClose}>
          <CustomNavLinkSmall>
            <Span>{t("About")}</Span>
          </CustomNavLinkSmall>
        </Link>
        <Link to="/products" onClick={onClose}>
          <CustomNavLinkSmall>
            <Span>{t("Products")}</Span>
          </CustomNavLinkSmall>
        </Link>
        <Link to="/infrastructure" onClick={onClose}>
          <CustomNavLinkSmall>
            <Span>{t("Infrastructure")}</Span>
          </CustomNavLinkSmall>
        </Link>
        <Link to="/quality" onClick={onClose}>
          <CustomNavLinkSmall>
            <Span>{t("Quality")}</Span>
          </CustomNavLinkSmall>
        </Link>
        <Link to="/contact-us" onClick={onClose}>
          <CustomNavLinkSmall>
            <Span>{t("Contact Us")}</Span>
          </CustomNavLinkSmall>
        </Link>
        <a href="https://wa.me/+917383481710?text=Hi%2C%20I'm%20interested%20in%20FIBC%20product!">
          <SvgIcon
            src="whatsapp_icon.svg"
            aria-label="logo"
            width="50"
            height="25"
          />
          <Span>+91 7383481710</Span>
        </a>
      </>
    );
  };

  return (
    <HeaderSection>
      <Container>
        <Row justify="space-between">
          <LogoContainer to="/" aria-label="homepage">
            <SvgIcon src="logo.svg" aria-label="logo" width="200" height="60" />
          </LogoContainer>
          <NotHidden>
            <MenuItem />
          </NotHidden>
        </Row>
        <div className="MenuDIV">
          <nav className="menu--right" role="navigation">
            <div className="menuToggle">
              <input type="checkbox" onClick={showDrawer} checked={visible} />
              <span className="ham_span"></span>
              <span className="ham_span"></span>
              <span className="ham_span"></span>
              <ul className="menuItem">
                <li>
                  <Link to="/about" onClick={showDrawer}>
                    <Span>{t("About")}</Span>
                  </Link>
                  <br />
                </li>
                <li>
                  <Link to="/products" onClick={onClose}>
                    <Span>{t("Products")}</Span>
                  </Link>
                  <br />
                </li>
                <li>
                  <Link to="/infrastructure" onClick={onClose}>
                    <Span>{t("Infrastructure")}</Span>
                  </Link>
                  <br />
                </li>
                <li>
                  <Link to="/quality" onClick={onClose}>
                    <Span>{t("Quality")}</Span>
                  </Link>
                  <br />
                </li>
                <li>
                  <Link to="/contact-us" onClick={onClose}>
                    <Span>{t("Contact Us")}</Span>
                  </Link>
                  <br />
                </li>
                <li>
                  <a href="https://wa.me/+917383481710?text=Hi%2C%20I'm%20interested%20in%20FIBC%20product!">
                    <SvgIcon
                      src="whatsapp_icon.svg"
                      aria-label="logo"
                      width="50"
                      height="25"
                    />
                    <Span>+91 7383481710</Span>
                  </a>
                </li>
              </ul>
            </div>
          </nav>
        </div>
      </Container>
    </HeaderSection>
  );
};

export default withTranslation()(Header);
