import { Row, Col, Image } from "antd";
import { withTranslation } from "react-i18next";
import { SvgIcon } from "../../common/SvgIcon";
import Container from "../../common/Container";

import i18n from "i18next";
import {
  FooterSection,
  Title,
  NavLink,
  Extra,
  LogoContainer,
  Para,
  Large,
  Chat,
  Empty,
  FooterContainer,
  Language,
  Label,
  LanguageSwitch,
  LanguageSwitchContainer,
} from "./styles";

interface SocialLinkProps {
  href: string;
  src: string;
}

const addresses1 = [
  {
    country: "USA",
    img: "/img/svg/usa.svg",
    address: "12, Bleeker street, John f Kennedy jersey city",
    phone: "+91 81416 81710",
  },
  {
    country: "NEW ZEALAND",
    img: "/img/svg/nz.svg",
    address: "22A Westminster Street, New Windsor, Auckland 0600.",
    phone: "+91 81416 81710",
  },
];

const addresses2 = [
  {
    country: "CANADA",
    img: "/img/svg/canada.svg",

    address: "6 Warwick drive, chatham, Ontario, N7MOTI",
    phone: "+91 81416 81710",
  },
  {
    country: "AUSTRALIA",
    img: "/img/svg/aus.svg",

    address: "74, Evans Street, Belmont NSW 2280, Australia.",
    phone: "+91 81416 81710",
  },
];

const Footer = ({ t }: any) => {
  const handleChange = (language: string) => {
    i18n.changeLanguage(language);
  };

  const SocialLink = ({ href, src }: SocialLinkProps) => {
    return (
      <a
        href={href}
        target="_blank"
        rel="noopener noreferrer"
        key={src}
        aria-label={src}
        style={{ padding: "0.7vw" }}
      >
        <SvgIcon src={src} width="25px" height="25px" />
      </a>
    );
  };

  return (
    <>
      <FooterSection>
        <Container>
          <Row justify="space-between">
            <Col lg={10} md={10} sm={20} xs={20}>
              <Language>{t("Contact")}</Language>
              <Large to="/">
                <Chat>{t("Ask us anything!")}</Chat>
              </Large>
              <a href="mailto:l.qqbadze@gmail.com">
                <Chat>{t(`Do you have any question?`)}</Chat>
              </a>
              <a href="mailto:l.qqbadze@gmail.com">
                <Chat>{t(`Let's Chat`)}</Chat>
              </a>
              <div style={{ display: "flex" }}>
                {" "}
                <a href="https://wa.me/+917383481710?text=Hi%2C%20I'm%20interested%20in%20FIBC%20product!">
                  <SvgIcon
                    src="whatsapp_icon.svg"
                    aria-label="logo"
                    width="50"
                    height="25"
                  />
                  <span>+917383481710</span>
                </a>
              </div>
            </Col>

            <Col lg={7} md={8} sm={20} xs={20}>
              <Title>{t("Our Offices")}</Title>
              {addresses1.map((i) => {
                return (
                  <div>
                    <Row align={"middle"}>
                      <Col lg={5} md={9} sm={7} xs={7}>
                        <Image src={i.img} preview={false} width={50} />
                      </Col>
                      <Col
                        lg={15}
                        md={15}
                        sm={15}
                        xs={15}
                        style={{ margin: "10px" }}
                      >
                        <h4>{i.country}</h4>
                        {i.address}
                        <br />
                        {i.phone}
                        <br />
                      </Col>
                    </Row>
                  </div>
                );
              })}
            </Col>
            <Col lg={7} md={8} sm={20} xs={20}>
              <br /> <br />
              {addresses2.map((i) => {
                return (
                  <div>
                    <Row align={"middle"}>
                      <Col lg={5} md={9} sm={7} xs={7}>
                        <Image src={i.img} preview={false} width={50} />
                      </Col>
                      <Col
                        lg={15}
                        md={15}
                        sm={15}
                        xs={15}
                        style={{ margin: "10px" }}
                      >
                        <h4>{i.country}</h4>
                        {i.address}
                        <br />
                        {i.phone}
                      </Col>
                    </Row>
                  </div>
                );
              })}
            </Col>
          </Row>
          <Row justify="space-between">
            <Col lg={8} md={8} sm={8} xs={8}>
              <Title>{t("VR FIBC")}</Title>
              <Large left="true" to="/about">
                {t("About")}
              </Large>
              <Large left="true" to="/products">
                {t("Products")}
              </Large>
              <Large left="true" to="/infrastructure">
                {t("Infrastructure")}
              </Large>
              <Large left="true" to="/quality">
                {t("Quality")}
              </Large>
            </Col>
            <Col lg={6} md={6} sm={12} xs={12}>
              <Label htmlFor="select-lang">{t("Language")}</Label>
              <LanguageSwitchContainer>
                <LanguageSwitch onClick={() => handleChange("en")}>
                  <SvgIcon
                    src="united-states.svg"
                    aria-label="homepage"
                    width="30px"
                    height="30px"
                  />
                </LanguageSwitch>

                <LanguageSwitch onClick={() => handleChange("es")}>
                  <SvgIcon
                    src="spain.svg"
                    aria-label="homepage"
                    width="30px"
                    height="30px"
                  />
                </LanguageSwitch>
              </LanguageSwitchContainer>
            </Col>
          </Row>
        </Container>
      </FooterSection>
      <Extra>
        <Container border={true}>
          <Row>
            <Col style={{ paddingTop: "3rem", width: "50%" }} span={12}>
              <NavLink to="/">
                <LogoContainer>
                  <Image
                    src="img/icons/logo1.png"
                    preview={false}
                    width={150}
                  />
                </LogoContainer>
              </NavLink>
              <SocialLink href="" src="facebook.svg" />
              <SocialLink href="" src="twitter.svg" />
              <SocialLink href="" src="linkedin.svg" />
              <SocialLink href="" src="medium.svg" />
            </Col>
            <Col span={12} style={{ paddingTop: "3rem", width: "50%" }}>
              <Image src="/img/svg/iso.png" preview={false} width={150} />
              {/* <div>ISO 9001: 2015, <br/>ISO 22000, <br/>ISO 21898</div> */}
            </Col>
          </Row>
        </Container>
      </Extra>
    </>
  );
};

export default withTranslation()(Footer);
