import { createGlobalStyle } from "styled-components";

export const Styles = createGlobalStyle`

    @font-face {
        font-family: "NotoSans-Regular";
        src: url("/fonts/NotoSans-Regular.ttf") format("truetype");
        font-style: normal;
    }

    @font-face {
        font-family: "NotoSans-Bold";
        src: url("/fonts/NotoSans-Bold.ttf") format("truetype");
        font-style: normal;
    }
    @font-face {
        font-family: "NotoSans-Medium";
        src: url("/fonts/NotoSans-Medium.ttf") format("truetype");
        font-style: normal;
    }


    body,
    html,
    a {
        font-family: 'NotoSans-Regular', sans-serif;
    }


    body {
        margin:0;
        padding:0;
        border: 0;
        outline: 0;
        background: #fff;
        overflow-x: hidden;
    }

    a:hover {
        color: #18216d;
    }

    input,
    textarea {
        border-radius: 4px;
        border: 0;
        background: rgb(241, 242, 243);
        transition: all 0.3s ease-in-out;  
        outline: none;
        width: 100%;  
        padding: 1rem 1.25rem;

        :focus-within {
            background: none;
            box-shadow: #2e186a 0px 0px 0px 1px;
        }
    }

    h1 {
        font-family: 'NotoSans-Bold', serif;
        color: #18216d;
        font-size: 56px;
        line-height: 1.18;

        @media only screen and (max-width: 890px) {
          font-size: 47px;
        }
      
        @media only screen and (max-width: 414px) {
          font-size: 32px;
        }
    }

    h3 {
        font-family: 'NotoSans-Bold', serif;
        color: #18216d;
        font-size: 36px;
        line-height: 1.18;

        @media only screen and (max-width: 890px) {
          font-size: 40px;
        }
      
        @media only screen and (max-width: 414px) {
          font-size: 28px;
        }
    }

    h4 {
        font-family: 'NotoSans-Bold', serif;
        color: #18216d;
        font-size: 22px;
        line-height: 1.18;

        @media only screen and (max-width: 890px) {
          font-size: 35px;
        }
      
        @media only screen and (max-width: 414px) {
          font-size: 22px;
        }
    }

    h5 {
        font-family: 'NotoSans-Bold', serif;
        color: #18216d;
        font-size: 19px;
        line-height: 1.17;

        @media only screen and (max-width: 890px) {
          font-size: 30px;
        }
      
        @media only screen and (max-width: 414px) {
          font-size: 18px;
        }
    }

    p {
        font-size: 21px;
        line-height: 1.18;

        @media only screen and (max-width: 890px) {
          font-size: 35px;
        }
      
        @media only screen and (max-width: 414px) {
          font-size: 22px;
        }
    }

    h1 {
        font-weight: 600;
    }

    a {
        text-decoration: none;
        outline: none;
        color: #2E186A;

        :hover {
            color: #2e186a;
        }
    }
    
    *:focus {
        outline: none;
    }

    .about-block-image svg {
        text-align: center;
    }

    .ant-drawer-body {
        display: flex;
        flex-direction: column;
        text-align: left;
        padding-top: 1.5rem;
    }

    .ant-drawer-content-wrapper {
        width: 300px !important;
    }
`;
